import React from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';

const Descripcion = ({ nombre, precio, descripcion, descripcionBreve}) => {
    return (
        <React.Fragment>
            <Typography variant="overline" align="center" color='primary' component="h1" id="aria-title" paragraph>
                VELVET chocolates
            </Typography>                  
            <Typography variant="h4" align="center" component="h2" gutterBottom>
                {nombre}
            </Typography>               
            
            <Chip 
                label={`$ ${precio}`} color='primary' 
                style={{fontSize: 22, color: '#fff', margin: '0 auto', marginBottom: 32, padding: '21px 4px'}}
            />                        
            
            <Typography variant="body1" align="justify" paragraph style={{fontWeight: 600}}>
                {descripcionBreve}                    
            </Typography>                  
            <Typography variant="body1" align="justify" paragraph> 
                {descripcion}
            </Typography>
            <Typography variant="body2" align="justify" style={{marginTop: 16}} >
                Todos los productos son realizados a pedido y carecen de conservante, por lo tanto mantenelos en un lugar fresco, seco, y alejado de los olores fuertes (una cava de vinos es ideal).
            </Typography>            
        </React.Fragment>
    );
};

Descripcion.propTypes = {
    nombre: PropTypes.string.isRequired,
    precio: PropTypes.number.isRequired,
    descripcion: PropTypes.string.isRequired,
    descripcionBreve: PropTypes.string.isRequired,
};

export default Descripcion;